<template>
    <div class="wrapper" :class="{ loading }">
        <div v-show="!loading && !error">
            <h1>Pago</h1>
            <h3>TOTAL: ${{ asigment.budget }}</h3>
            <div id="paypal-button"></div>
        </div>

        <span v-if="error" class="error" role="alert">{{ error }}</span>
    </div>
</template>

<script>
export default {
    props: ['asigment'],

    data: () => ({
        loading: true,
        error: null,
    }),

    mounted() {
        const script = document.createElement('script')

        script.onload = () => {
            this.loading = false
            this.setupPayPalButton()
        }

        script.onerror = () => {
            this.loading = false
            this.error =
                'Hubo un error, asegurate de que estas conectado a internet.'
        }

        script.async = true
        script.src = 'https://www.paypalobjects.com/api/checkout.js'
        document.head.appendChild(script)
    },

    methods: {
        setupPayPalButton() {
            paypal.Button.render(
                {
                    // Configure environment
                    env: 'sandbox',
                    locale: 'es_ES',
                    style: {
                        size: 'large',
                        color: 'gold',
                        shape: 'pill',
                    },
                    commit: true, // Enable Pay Now checkout flow (optional)
                    payment: this.handlePayment.bind(this), // Set up a payment
                    onAuthorize: this.handleOnAuthorize.bind(this), // Execute the payment
                },
                '#paypal-button'
            )
        },

        async handlePayment(data, actions) {
            const url = route('paymnet.create')
            const { id } = await actions.request.post(url)

            return id
        },

        handleOnAuthorize(data, actions) {
            const { paymentID, payerID } = data
            const url = route('payment.execute')
            return actions.request.post(url, { paymentID, payerID }).then()
        },

        async goToRoom() {
            try {
                const { id } = this.asigment
                const teacherId = this.currentTeacher.id
                const url = route('asigment.update')
                await this.$http.put(url, { teacher_id: teacherId })
                window.location.href = route('room', id)
            } catch (error) {
                console.error(error.response || error)
            }
        },
    },
}
</script>

