@import '~@/sass/_globals.scss';

.wrapper {
    position: relative;
    min-height: 50vh;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.wrapper.loading::before {
    content: 'Un momento por favor...';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    text-align: center;
    font-size: 2rem;
    font-weight: get('bold', $font-weights);
}
</style>
